body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Commissioner", "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222831;
  color: #e0e0e0;
}

ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
  display: flex;
}

li {
  float: left;
  align-self: center;
}

li {
  display: block;
  padding: 0px 12px 0px 12px;
  font-size: 18px;
  font-weight: 600;
  transition: 0.2s;
}

.header-link {
  display: inline-block;
  text-decoration: none;
  text-decoration-skip-ink: auto;
  color: inherit;
  position: relative;
  cursor: pointer;
  border-bottom: 2px #eeeeee solid;
}

.header-link:hover {
  text-decoration: none;
  color: #d65a31;
  border-bottom: 2px #d65a31 solid;
}

li:hover {
  color: #d65a31;
}

.resume-link > a {
  border: 2px solid #d65a31;
  border-radius: 3px;
  color: #d65a31;
  margin-left: 15px;
  padding: 7px;
}
.resume-link > a:hover {
  background-color: rgba(214, 90, 49, 0.1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  color: #e0e0e0;
  width: 100%;
  height: 10vh;
  z-index: 100;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-offset {
  background-color: rgba(34, 40, 49, 0.95);
}

.logo {
  width: 35px;
}

.landing {
  width: 100%;
  color: #eeeeee;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 80%;
}

.title {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  font-size: 72px;
  font-weight: 800;
  color: #d65a31;
}

.lead {
  color: #65686d;
  font-weight: 700;
  font-size: 38px;
}

.contact-icon {
  font-size: 32px;
}

.landing-artwork-container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  width: 50%;
}

.landing-artwork {
  width: 100%;
}

@media (max-width: 756px) {
  .landing {
    flex-direction: column-reverse;
    justify-content: start;
    align-items: center;
  }

  .landing-artwork-container {
    margin-bottom: 5vh;
    margin-top: 5vh;
  }

  .title {
    font-size: 52px;
  }

  .lead {
    font-size: 24px;
  }

  .contact-icon {
    font-size: 24px;
  }
}

.landing-button {
  color: #d65a31;
  background-color: transparent;
  border: 2px solid #d65a31;
  font-weight: 500;
  padding: 10px 20px;
  font-size: 18px;
  border-radius: 5px;
  transition: 0.2s;
  margin-top: 5vh;
}
.landing-button:hover {
  background-color: rgba(214, 90, 49, 0.1);
}

.landing-button:focus {
  outline: none;
}

.heading {
  font-weight: 300;
  font-size: 40px;
  margin-bottom: 5px;
}

.underline {
  margin-top: 0px;
  text-align: left;
  margin-left: 0px;
  margin-bottom: 2rem;
  width: 30%;
  height: 7px;
  background-color: #d65a31;
}

.resume,
.connect {
  font-size: 1.5rem;
  font-weight: 300;
}

.resume > span {
  color: #d65a31;
  cursor: pointer;
  font-weight: normal;
}

.connect > a:not(:first-child) {
  padding-left: 8px;
}

i {
  cursor: pointer;
  color: #65686d;
  transition: 0.3s;
}

i:hover {
  color: #d65a31;
}

.section {
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-text {
  text-align: left;
  font-size: 20px;
}

.about-skills {
  display: flex;
}

.about-skills > p {
  margin-right: 100px;
}

.footer {
  justify-self: end;
}

.slick-prev {
  left: 0;
  position: absolute;
  z-index: 100;
  margin-left: 10px;
  border: solid #222831;
  border-width: 0 3px 3px 0;
  display: inline-block;
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.slick-next {
  right: 0;
  margin-right: 10px;
  border: solid #222831;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.slick-prev:before {
  content: none;
}

.slick-next:before {
  content: none;
}

.project-img-container {
  display: block !important;
}

.project-img {
  display: block;
  background-repeat: no-repeat;
  width: 100%;
  height: auto;
  filter: grayscale(100%) contrast(1) brightness(90%);
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: 0.5s ease;
  background-color: #d65a31;
  opacity: 0.7;
}

.project-img-container:hover .overlay {
  opacity: 0;
}

.project-img-container:hover .project-img {
  filter: none;
}

.card-heading {
  font-size: 32px;
  font-weight: 300;
}

.contact-email {
  text-align: center;
  font-weight: 300;
  font-size: 28px;
  transition: color 0.3s;
  margin-bottom: 30px;
  display: block;
}

.contact-email:hover {
  color: #d65a31;
  cursor: pointer;
}

.copyright-text {
  justify-self: flex-end;
  text-align: center;
  font-weight: 300;
  color: #65686d;
}
